:root {
  --colorgrey: #e1e7ed;
  --dropcolor: #00000040;
  --fontcolor: #3d4e61;
}

body {
  font-family: Avenir, sans-serif;
}

.search {
  margin-top: 20px;
}

.search__input {
  box-sizing: border-box;
  color: var(--fontcolor);
  border: 1px solid var(--colorgrey);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search__input--big {
  background-position: 26px;
  background-size: 19px 19px;
  height: 70px;
  padding: 0 62px;
  font-size: 16px;
}

.search__input--small {
  background-position: 13px;
  background-size: 11px 11px;
  height: 42px;
  padding: 0 33px;
  font-size: 14px;
}

.search__input:hover {
  box-shadow: 0 3px 8px 0 var(--dropcolor);
  font-family: Avenir-Book, sans-serif;
}

.search__input:focus {
  box-shadow: 0 4px 4px var(--dropcolor);
  font-family: Avenir-Heavy, sans-serif;
}

.search__input::placeholder {
  color: var(--fontcolor);
}

